import React from 'react';
import 'tachyons';
import './Navigation.css'
import Resume from './Resume.pdf';
const Navigation = ({ClickImagebar,ClickAboutMe, ClickAchievements , ClickProjects}) => {
    return(
        <div>
            <nav className='bar '>
                <a href={Resume} className='but shadow-5 link pointer dim grow dib br3 ma2 tc' >My Resume</a>
                <a onClick={ClickProjects} className='but shadow-5 link pointer dim grow dib br3 ma2 tc'>My Projects</a>
                <a onClick={ClickAchievements} className='but shadow-5 link pointer dim grow dib br3 ma2 tc'>My Achievements</a>
                <a className='but shadow-5 link pointer dim grow dib br3 ma2 tc' onClick={ClickAboutMe}>About Me</a>
                <a className='but shadow-5 link pointer dim grow dib br3 ma2 tc' onClick={ClickImagebar}>Home</a>
            </nav>
            <span className='bar1 '>
                <a href={Resume} className='but1 shadow-5 link pointer dim grow dib br3 ma2 tc'>My Resume</a>
                <a onClick={ClickProjects} className='but1 shadow-5 link pointer dim grow dib br3 ma2 tc'>My Projects</a>
                <a onClick={ClickAchievements} className='but1 shadow-5 link pointer dim grow dib br3 ma2 tc'>My Achievements</a>
                <a className='but1 shadow-5 link pointer dim grow dib br3 ma2 tc' onClick={ClickAboutMe}>About Me</a>
                <a className='but1 shadow-5 link pointer dim grow dib br3 ma2 tc' onClick={ClickImagebar}>Home</a>
            </span>
        </div>
    );
}

export default Navigation;